body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: grid;

  grid-template-columns: auto 1fr;
  column-gap: 20px;
  max-width: 820px;
  margin: 0 auto;
}

.container table {
  width: 100%;
}

.container thead th,
tr td {
  border-bottom: 1px solid #ccc;
  text-align: left;
  height: 50px;
  padding: 4px 8px;
}

.container tr td button {
  height: 35px;
  padding: 0 30px;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  color: #fff;
  background-color: rgb(29, 77, 236);
}

.container tr td button:hover {
  color: #fff;
  background-color: rgba(20, 71, 240, 0.9);
}

.container form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  width: 280px;
  margin-top: 15px;
}

.container form label {
  margin: 8px 0;
}

.container form input {
  height: 30px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 2px 10px;
  font-size: 16px;
}

.container form input:last-child {
  height: 35px;
  margin-top: 15px;
  padding: 0 30px;
  border: none;
  border-radius: 4px;
  font-weight: 600;
  color: #fff;
  background-color: rgb(29, 77, 236);
}

.container form input:last-child:hover {
  color: #fff;
  background-color: rgba(20, 71, 240, 0.9);
}
